import React, { Component } from 'react'
class Input extends Component{
    constructor(props){
        super(props);

        this.handleChange=this.handleChange.bind(this);
    }
    componentDidMount(){
        this.refs.inp.focus();
    }
    handleChange=()=>{
        var value=this.refs.inp.value;
          var obj={
              title:value,
              done:false
          };
          this.refs.inp.value='';
          this.props.addTodo(obj);
    }
    render(){
        return (
            <div>
                <label htmlFor='inp'>todolist</label>
                <input  id='inp'  ref="inp"/>
                <button type="submit" onClick={this.handleChange}>添加</button>
            </div>
        )
    }
}

export default Input;