import React, { Component } from 'react';
import Input from './Input';
import List from './List';
class TodoList extends Component {
    constructor(props){
        super(props);
        this.state={
            inpValue:'',
            todos:[]
        }
    }
    componentDidMount(){
        let todos=localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    addTodo = (newTodos)=>{
        if(this.state.inputValue!==''){
            this.setState({
                todos:[newTodos,...this.state.todos]       
        })
            this.setState({inputValue:''})
            }
    }
    removeTodo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>index!=idx)
        });
    }
    toggle=(index)=>{
        let newTodos=JSON.parse(JSON.stringify(this.state.todos));
        newTodos[index].done=!newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }

    renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
            return  <li>
                    <input type='checkbox' checked={done} 
                    onClick={()=>this.toggle(idx)}/>
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button onClick={()=>this.removeTodo(idx)}>删除</button>
                </li>   
            }
        })
    }

    render() {
        const {todos,inpValue}=this.state;
        let arr1=todos.filter(item=>!item.done);
        return (
            <div>
                <Input addTodo={this.addTodo.bind(this)}/>
                <h2>正在进行{arr1.length}</h2>
                <List todos={this.state.todos}
                        renderTodos={this.renderTodos}
                />
            </div>
        )
    }
}

export default TodoList;